const SunnySideModal = ({ closesunnySideModal }) => {
  return (
    <div
      id="popup-modal"
      tabIndex="-1"
      className=" bg-[#00000098] modal-scrollbar  overflow-y-visible overflow-x-hidden fixed top-0 right-0 left-0 z-50 inset-0  h-auto md:h-auto flex justify-center items-center"
      aria-hidden="true"
    >
      <div className="relative top-[10%]  md:top-[10%] p-8   md:w-[650px]   lg:w-[756px] max-w-[90vw] h-[fit-content]">
        <div className="relative  pb-6 rounded-lg shadow bg-[#242424] dark:bg-[#E3E3E3]  border-[2px] border-[#6368ca] ">
          <div className="flex justify-between   mx-4 sm:mx-8 relative text-xl items-center  pt-5 text-[#E3E3E3] dark:text-[#1E1E1E] ">
            <h1 className="font-bold sm:tracking-wider">SunnySide</h1>
            <button
              onClick={() => {
                closesunnySideModal();
              }}
              type="button"
              className=" text-[#E3E3E3] bg-transparent transition-all duration-500 hover:bg-[#51539cc7] rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-[#9499ff] dark:hover:text-white"
              data-modal-toggle="popup-modal"
            >
              <svg
                aria-hidden="true"
                className=" fill-[#E3E3E3] dark:fill-[#1E1E1E] w-6 h-6"
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fillRule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clipRule="evenodd"
                ></path>
              </svg>
              <span className="sr-only">Close modal</span>
            </button>
          </div>

          <div class=" pt-3 pb-6 pr-6 pl-6 flex justify-center">
            <div className="">
              <img
                className=" rounded-[0.199rem]  md:w-[690px] h-[auto]"
                src={sunnysidecover}
                alt=""
              />
            </div>
          </div>

          <div className="tools  mx-6 pb-12">
            <h2 className="text-[#E3E3E3] dark:text-[#1E1E1E]  pb-3 text-xl font-semibold">
              Tools Used
            </h2>
            <div className="flex gap-3 ml-3 overflow-hidden flex-wrap ">
              {/* onclick small info nbox  */}
              <button className="text-[#E3E3E3] dark:text-[#1E1E1E]  font-light bg-[#9499ff] transition-all duration-500 hover:bg-[#51539cc7] tracking-wide rounded-full py-[.18rem] px-2 text-[.8rem] italic ">
                HTML5
              </button>
              <button className="text-[#E3E3E3] dark:text-[#1E1E1E]  font-light bg-[#9499ff] transition-all duration-500 hover:bg-[#51539cc7]  tracking-wide rounded-full py-[.18rem] px-2 text-[.8rem] italic ">
                CSS
              </button>
              <button className="text-[#E3E3E3] dark:text-[#1E1E1E]  font-light bg-[#9499ff] transition-all duration-500 hover:bg-[#51539cc7]  tracking-wide rounded-full py-[.18rem] px-2 text-[.8rem] italic ">
                JavaScript
              </button>
            </div>
          </div>

          <div className="project-desc text-[#E3E3E3] dark:text-[#1E1E1E]  mx-6 pb-8">
            <h2 className=" pb-3 text-lg font-semibold">Project description</h2>
            <p className="text-[.9rem] itali font-light">
              A Front-end Development project using mainly plain HTML, CSS and
              JavaScript from some simple functionality
            </p>
          </div>

          <div className="preview text-[#9499ff] mx-6 pb-8 border-b-[#9499ff] border-b-2">
            <a
              href="https://sunnysideui.netlify.app/"
              rel="noreferrer"
              target="_blank"
              className="preview-link pb-3 text-lg font-semibold underline "
            >
              Preview Website
            </a>
          </div>

          <div className="mx-6 mt-3 flex text-[#9499ff] justify-center items-center gap-3">
            <a
              target="_blank"
              rel="noreferrer"
              className="text-sm hover:underline"
              href="https://github.com/d2ni3l/Sunnyside"
            >
              GitHub repo
            </a>
            <img src={githubIcon} className="w-6" alt="" />
          </div>
        </div>
      </div>
    </div>
  );
};

